
import React, {useEffect, useState} from 'react';
import {connect} from "react-redux";
import Movie from "./Movie";

import {api} from "./api";
import './App.css';
import {setGenresAC, setMoviesAC} from "./FilmsReducer";

const MovieContainer=(props)=>{



    const [movies, setMovies] = useState([])

    useEffect(() => {

        api.getFilms().then((data) => {
            return data.results
        }).then((result) => {
            props.setMoviesAC(result);
        });
        api.getGenre().then((data)=>{

            props.setGenresAC(data.genres);

        })


    }, [])

    return (<div className={"innerBody"}>
        <div className="App">


            {props.describe.length > 1 ? props.describe.map((u) => <Movie genres={props.genres} key={u.id} film={u}  />) : <div>PRELOADER</div>}
    </div>)
    </div>);
}



let stateGo=(state)=>{

    return {

    describe:state.films.details,
        genres:state.films.genres,
    }
};

const MovieContainerR=connect(stateGo,{
    setMoviesAC,
    setGenresAC,
})(MovieContainer);
export default MovieContainerR;