import * as React from 'react';
import hd from "./Header.module.css"
import logo from "./images/logo.png"

const Header=(props:any)=>{

    return(<div className={hd.HeaderBody}>

        <div className={hd.navBar}>
            <img src={logo} className={hd.logo}></img>
            <div className={hd.navBarMain}>

            <a data-content={"ТБ"} href={"#"} >ТБ</a>
            <a data-content={"Фільми"} href={"#"}>Фільми</a>
            <a data-content={"Прем"} href={"#"}>Прем'єри</a>
            <a data-content={"Тарифи"} href={"#"}>Тарифи</a>
            <a data-content={"Як підключити"} href={"#"}>Як підключити</a>
            <a data-content={"Промокод"} href={"#"}>Промокод</a>
            <a data-content={"Ще"} href={"#"}>Ще</a>
            </div>
        <div className={hd.navBarOther}>
            <a>F</a>
            <a>The</a>
            <a>UKR</a>
            <a>__Увійти</a>
        </div>
        </div>


</div>)

}

export default Header;