import * as React from 'react';
import { api } from './api';
import "./Movie.css"
import {useEffect} from "react";
import {useState} from "react";
const API_IMAGES="https://image.tmdb.org/t/p/w1280";
const Movie=(props:any)=>{
let getGenre=(genresToFind:any)=> {
    let genres = [];


        for (let genre = 0; genre < genresToFind.length; genre++) {
            for (let i = 0; i < props.genres.length; i++) {
                if (props.genres[i].id == props.film.genre_ids[genre]) {
                    genres.push(props.genres[i].name)
                }
            }
        }
        return genres;
};
let getShortGenre=(genres2:any)=>{
    let strGenre="";
    debugger;
    for (let i=0;i<genres2.length;i++){
         strGenre=strGenre+genres2[i];
        if (i!=genres2.length-1) strGenre+=",";
    }
    if (strGenre.length<25){
        return   strGenre;} else return strGenre.slice(0,25)+"..."}

return(<div className={"movie"} id={"movieID"}>


        {props.film.poster_path!=null && props.genres.length > 1 ?
<div>
    <div className={"divTR"} >    <img className={"transf"} src={API_IMAGES+props.film.poster_path}></img>
    </div>
            <div className={"description"}>
            <h4 className={"title"}>{props.film.original_title.length<20?props.film.original_title:props.film.original_title.slice(0,20)+"..."}</h4>
            <h5 className={"genre"}>{" "+getShortGenre(getGenre(props.film.genre_ids))+" "}</h5>
              </div>
</div> :null}





</div>)

}

export default Movie