import React from "react";

let initialState={
    details :[],
    genres:[]
};

const FilmsReducer=(state=initialState,action)=>{

    switch (action.type) {

    case "setMovies":{
        let stateCopy={...state};
        stateCopy.details=action.num;

        return stateCopy;
    }
    case "setGenres":{

        let stateCopy={...state};
        stateCopy.genres=action.genres;

        return stateCopy;
    }
    default:{
        return state;
    }
}

};

export const setMoviesAC=(num)=>{

    return{
        type:"setMovies",
        num:num,
    }
};
export const setGenresAC=(genres)=>{

    return{
        type:"setGenres",
        genres:genres,
    }
};

export default FilmsReducer;