import * as axios from "axios";
const API_KEY="&api_key=44474131d671c8b9246cb0ec32cc4a16";

const API_SEARCH="https://api.themoviedb.org/3/search/movie?&api_key=04c35731a5ee918f014970082a0088b1&query="

const instance = axios.create({

    baseURL: "https://api.themoviedb.org/3",

});

export const api={

    getFilms(){
            return instance.get(`/movie/popular?page=${1}`+API_KEY).then((response)=>{return response.data});

    } ,
    getGenre(){
            return instance.get(`/genre/movie/list?p`+API_KEY).then((response)=>{return response.data});

    }
};